<template>
    <div class="">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item><i class="el-icon-message"></i> 消息中心</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <el-table
              :data="datas"
              style="width: 100%;margin-top:20px;">
              <el-table-column
                type="index"
                width="100px"
                label="序号">
              </el-table-column>
              <el-table-column
                prop="title"
                label="课程设计">
              </el-table-column>
              <el-table-column
                prop="men"
                label="发送人">
              </el-table-column>
              <el-table-column
                prop="content"
                label="消息内容">
              </el-table-column>
              <el-table-column
                prop="date"
                label="发送时间">
              </el-table-column>
              <el-table-column label="操作" width='150px'>
                 <el-button size="small" type="primary">进入作业</el-button>
                </el-table-column>
            </el-table>

            <div class="pagination">
                <el-pagination
                  background
                  layout="prev, pager, next"
                  :total="1000">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'tabs',
        data() {
            return {
                message: 'first',
                showHeader: false,
                datas: [{
                    men:'李老师',
                    date: '2018-04-19 20:00:00',
                    title:'2019级第一次课程设计',
                    content: '【2019级第一次课程设计】该系统将于今晚凌晨2点到5点进行升级维护',
                },{
                    men:'李老师',
                    date: '2018-04-19 21:00:00',
                    title:'2019级第一次课程设计',
                    content: '今晚12点整发大红包，先到先得',
                }]
            }
        },
        methods: {
            handleRead(index) {
                const item = this.unread.splice(index, 1);
                console.log(item);
                this.read = item.concat(this.read);
            },
            handleDel(index) {
                const item = this.read.splice(index, 1);
                this.recycle = item.concat(this.recycle);
            },
            handleRestore(index) {
                const item = this.recycle.splice(index, 1);
                this.read = item.concat(this.read);
            }
        },
        computed: {
            unreadNum(){
                return this.unread.length;
            }
        }
    }

</script>

<style>
.message-title{
    cursor: pointer;
}
.handle-row{
    margin-top: 30px;
}
</style>




